"use client"

import { useState } from 'react'
import Layout from '@/components/shared/Layout'
import { Card } from "@/components/ui/card"
import { Button } from "@/components/ui/button"
import { Badge } from "@/components/ui/badge"
import { Progress } from "@/components/ui/progress"
import {
  Select,
  SelectContent,
  SelectItem,
  SelectTrigger,
  SelectValue,
} from "@/components/ui/select"

interface AIAlert {
  id: string
  type: 'safety' | 'efficiency' | 'maintenance' | 'security'
  severity: 'critical' | 'high' | 'medium' | 'low'
  message: string
  location: string
  timestamp: string
  status: 'active' | 'investigating' | 'resolved'
}

interface MetricData {
  label: string
  value: number
  trend: 'up' | 'down' | 'stable'
  change: number
}

export default function AIMonitoringPage() {
  const [selectedSystem, setSelectedSystem] = useState<string>('all')

  const alerts: AIAlert[] = [
    {
      id: "ALT-001",
      type: "safety",
      severity: "high",
      message: "Unauthorized personnel detected in restricted area",
      location: "Zone B",
      timestamp: "2 minutes ago",
      status: "active"
    },
    {
      id: "ALT-002",
      type: "efficiency",
      severity: "medium",
      message: "Suboptimal traffic flow detected at loading bay",
      location: "Loading Bay A",
      timestamp: "5 minutes ago",
      status: "investigating"
    },
    {
      id: "ALT-003",
      type: "maintenance",
      severity: "low",
      message: "Predictive maintenance required for Truck #123",
      location: "Fleet",
      timestamp: "10 minutes ago",
      status: "resolved"
    }
  ]

  const metrics: MetricData[] = [
    {
      label: "Safety Score",
      value: 92,
      trend: "up",
      change: 3.5
    },
    {
      label: "Operational Efficiency",
      value: 87,
      trend: "up",
      change: 2.1
    },
    {
      label: "System Health",
      value: 95,
      trend: "stable",
      change: 0
    },
    {
      label: "Security Index",
      value: 89,
      trend: "down",
      change: -1.2
    }
  ]

  const getSeverityColor = (severity: AIAlert['severity']) => {
    const colors = {
      critical: 'bg-red-100 text-red-800',
      high: 'bg-orange-100 text-orange-800',
      medium: 'bg-yellow-100 text-yellow-800',
      low: 'bg-blue-100 text-blue-800'
    }
    return colors[severity]
  }

  const getStatusColor = (status: AIAlert['status']) => {
    const colors = {
      active: 'bg-red-100 text-red-800',
      investigating: 'bg-yellow-100 text-yellow-800',
      resolved: 'bg-green-100 text-green-800'
    }
    return colors[status]
  }

  const getTrendColor = (trend: MetricData['trend']) => {
    const colors = {
      up: 'text-green-600',
      down: 'text-red-600',
      stable: 'text-blue-600'
    }
    return colors[trend]
  }

  const getTrendArrow = (trend: MetricData['trend']) => {
    const arrows = {
      up: '↑',
      down: '↓',
      stable: '→'
    }
    return arrows[trend]
  }

  return (
    <Layout title="AI Monitoring System">
      <div className="space-y-6">
        {/* Key Metrics */}
        <div className="grid grid-cols-1 md:grid-cols-2 lg:grid-cols-4 gap-4">
          {metrics.map((metric, index) => (
            <Card key={index} className="p-6">
              <div className="space-y-2">
                <h3 className="text-sm font-medium text-gray-500">{metric.label}</h3>
                <div className="flex items-baseline justify-between">
                  <p className="text-2xl font-bold">{metric.value}%</p>
                  <span className={`flex items-center ${getTrendColor(metric.trend)}`}>
                    {getTrendArrow(metric.trend)} {Math.abs(metric.change)}%
                  </span>
                </div>
                <Progress value={metric.value} />
              </div>
            </Card>
          ))}
        </div>

        {/* AI Insights and Alerts */}
        <div className="grid grid-cols-1 lg:grid-cols-3 gap-6">
          {/* Active Alerts */}
          <Card className="lg:col-span-2 p-6">
            <div className="flex justify-between items-center mb-4">
              <h2 className="text-xl font-semibold">Active Alerts</h2>
              <Select value={selectedSystem} onValueChange={setSelectedSystem}>
                <SelectTrigger className="w-[180px]">
                  <SelectValue placeholder="Filter by system" />
                </SelectTrigger>
                <SelectContent>
                  <SelectItem value="all">All Systems</SelectItem>
                  <SelectItem value="safety">Safety Systems</SelectItem>
                  <SelectItem value="efficiency">Efficiency</SelectItem>
                  <SelectItem value="maintenance">Maintenance</SelectItem>
                </SelectContent>
              </Select>
            </div>
            <div className="space-y-4">
              {alerts.map((alert) => (
                <Card key={alert.id} className="p-4">
                  <div className="flex justify-between items-start">
                    <div className="space-y-1">
                      <div className="flex items-center space-x-2">
                        <Badge className={getSeverityColor(alert.severity)}>
                          {alert.severity.toUpperCase()}
                        </Badge>
                        <Badge className={getStatusColor(alert.status)}>
                          {alert.status.toUpperCase()}
                        </Badge>
                      </div>
                      <p className="font-medium">{alert.message}</p>
                      <div className="text-sm text-gray-500">
                        Location: {alert.location} • {alert.timestamp}
                      </div>
                    </div>
                    <Button variant="outline" size="sm">
                      Investigate
                    </Button>
                  </div>
                </Card>
              ))}
            </div>
          </Card>

          {/* AI Analysis */}
          <Card className="p-6">
            <h2 className="text-xl font-semibold mb-4">AI Analysis</h2>
            <div className="space-y-6">
              <div>
                <h3 className="text-sm font-medium text-gray-500 mb-2">Pattern Recognition</h3>
                <div className="space-y-2">
                  <div className="flex justify-between">
                    <span>Traffic Patterns</span>
                    <span className="text-green-600">98% Normal</span>
                  </div>
                  <div className="flex justify-between">
                    <span>Safety Compliance</span>
                    <span className="text-green-600">95% Adherence</span>
                  </div>
                  <div className="flex justify-between">
                    <span>Equipment Usage</span>
                    <span className="text-yellow-600">87% Optimal</span>
                  </div>
                </div>
              </div>

              <div>
                <h3 className="text-sm font-medium text-gray-500 mb-2">Predictive Analytics</h3>
                <div className="space-y-2">
                  <Card className="p-3">
                    <div className="text-sm">
                      <p className="font-medium">Maintenance Forecast</p>
                      <p className="text-gray-500">3 vehicles due for service next week</p>
                    </div>
                  </Card>
                  <Card className="p-3">
                    <div className="text-sm">
                      <p className="font-medium">Traffic Prediction</p>
                      <p className="text-gray-500">Peak expected in 2 hours</p>
                    </div>
                  </Card>
                </div>
              </div>
            </div>
          </Card>
        </div>

        {/* System Performance */}
        <Card className="p-6">
          <h2 className="text-xl font-semibold mb-4">System Performance</h2>
          <div className="grid grid-cols-1 md:grid-cols-2 lg:grid-cols-4 gap-4">
            {[
              { label: "CPU Usage", value: 45 },
              { label: "Memory Usage", value: 62 },
              { label: "Network Load", value: 28 },
              { label: "Storage Usage", value: 75 }
            ].map((metric, index) => (
              <div key={index} className="space-y-2">
                <div className="flex justify-between text-sm">
                  <span>{metric.label}</span>
                  <span>{metric.value}%</span>
                </div>
                <Progress value={metric.value} />
              </div>
            ))}
          </div>
        </Card>
      </div>
    </Layout>
  )
}
